
.addcart {
	width            : 30%;
	background-color : rgb(0, 129, 255);
	border-left      : 10px solid #fff;
	color            : #fff;
	line-height      : 40px;
	padding-left     : .5rem;
	padding-right    : .5rem;
	text-align       : center;
	display          : inline-block;
}
.addcart i {
	padding-right : 0.5rem;
}
.inner{
}
.h-desc{
	max-height : 10rem;
	overflow-y : auto;
}
.h-items{
	width       : 26rem;
	overflow-y  : hidden;
	height      : 140px;
	overflow-x  : scroll;
	white-space : nowrap;
	margin      : 1rem;
}
.h-items .items {
	width   : 100px;
	margin  : 0 1rem;
	display : inline-block;
}
.h-items .items .itemTitle {
	font-weight : bold;
}
.h-items .items img {
	width : 100%;
}
.slide-fade-enter-active {
  	transition: all .3s ease;
}
.slide-fade-leave-active {
	transition : all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-active {
	-webkit-transform : translateX(10px);
	        transform : translateX(10px);
	opacity   : 0;
}



