
.ui.grid>.row>.column.subBox{
	padding: 0 0.5rem;
}
.ui.grid>.row>.column.subBox h2 {
	line-height : 350px;
	font-size   : 34px;
	color       : #fff;
}
.ui.grid>.row>.column.subBox i {
	font-size : 14px;
	color     : #fff;
	position  : absolute;
	right     : 0.5rem;
	bottom    : 0.5rem;
}
.ui.grid>.row>.column.subBox .box{
	height  : 107px;
	padding : 3rem 0;
}
.ui.grid>.row>.column.subBox h3 {
	color: #fff;
}



