
.clearfloat{
	clear    : both;
	display  : table;
	overflow : hidden;
	height   : 0;
	content  : '';
}
.orderList{
	margin    : 10px 0;
	font-size : 14px;
	font-size : 12px;
}
.orderList .orderText{
	text-align    : center;
	margin-left   : 5px;
	margin-top    : 0px;
	border-bottom : 1px solid #ccc;
}
.orderList .lab{
	background-color : #f5f5f5;
	width            : 100%;
	margin-top       : 30px;
	border-top       : 1px solid #333;
	border-bottom    : 1px solid #333;
	font-family      : myFontBoldEN;
	padding          : 0 10px;
	line-height      : 40px;
}
.orderList .lab>div{
	display      : inline-block;
	margin-right : 10px;
}
.orderList .status{
	float : right;
	color : #0025f5;
}
.orderList .status i{
	margin-right : 10px;
}
.orderList .list .listItem{
	border-bottom : 1px solid #ccc;
	padding       : 20px 0 10px;
}
.orderList .product{
	margin-left : 5px;
}
.orderList .listItem .img{
	width      : 50px;
	height     : 50px;
	margin-top : 15px;
}
.orderList .listItem .twelve{
	margin-top : 25px;
}
.orderList .listItem .orderText{
	line-height : 150px;
}
.orderList .price{
	text-align  : right;
	font-size   : 16px;
	padding     : 14px 14px 0px;
	font-family : myFontBoldEN;
}



