
.address{
	font-size : 12px;
}
.address i{
	margin-right : 10px;
	font-size    : 15px;
}
.address .title{
	font-family   : myFontBoldEN;
	line-height   : 50px;
	font-size     : 16px;
	border-bottom : 1px solid #ccc;
}
.addressTitle{
	font-size        : 14px;
	background-color : #d7f4f2;
	color            : #000;
	line-height      : 50px;
	margin           : 20px;
}
.addressTitle .fa{
	margin-right : 10px;
	font-size    : 15px;
}
.addressList .item{
	padding       : 14px 20px;
	border-bottom : 1px solid #eee;
	font-size     : 13px;
}
.addressList .itemInfo{
	text-align  : left;
	line-height : 30px;
}
.addressList .type{
	border      : 1px solid #26a2ff;
	color       : #26a2ff;
	margin-left : 10px;
	padding     : 1px 5px;
	font-size   : 11px;
}
.addressList .radio{
	outline      : none;
	margin-right : 5px;
	position     : relative;
	top          : 2px;
}
.addressList .item .bottom{
	padding : 20px 0;
}
.addressList .bottom i{
	margin-right: 5px;
}
.addressList .bottom .btn{
	display : inline-block;
	margin-left: 20px;
}



