
.order{
	width      : 100%;
	text-align : left;
}
.order .orderTitle{
	background-color : #3ecabf;
	height           : 60px;
	line-height      : 50px;
	text-align       : center;
	color            : #fff;
	font-family      : myFontBoldEN;
}
.order .content{
	margin-left : -10px;
}
.order .li-tab{
	background-color : #d7f4f2;
	text-align       : center;
	padding          : 0px 14px;
	font-size        : 12px;
}
.order .li-tab i{
	font-size     : 20px;
	margin-bottom : 5px;
	display       : block;
}



