
.newAddress{
	height    : 100%;
	overflow  : scroll;
	font-size : 12px;
}
.newAddress .title{
	font-family   : myFontBoldEN;
	line-height   : 50px;
	font-size     : 16px;
	border-bottom : 1px solid #ccc;
}
.newAddress .infor{
	text-align  : left;
	list-style  : none;
}
.newAddress .infor li{
	border-bottom : 1px solid #eee;
	padding       : 10px 20px;
	line-height   : 30px;
}
.newAddress .infor .inputType{
	outline          : none;
	background-color : #fff;
	width            : 64vw;
	border           : none;
	line-height      : 40px;
}
.newAddress #area{
	display : inline-block;
}
.newAddress .infor select{
	padding   : 0px;
	font-size : 12px;
	width     : auto;
	display   : inline-block;
	border    : none;
}
.newAddress .infor .btn{
	display          : inline-block;
	background-color : #eee;
	line-height      : 25px;
	padding          : 0px 10px;
	text-align       : center;
	margin-right     : 15px;
	border-radius    : 3px;
}
.newAddress .infor .type{
	width: 100px;
}
.newAddress .save{
	background-color : rgb(255,181,62);
	font-family      : myFontBoldEN;
	width            : 300px;
	line-height      : 40px;
	text-align       : center;
	margin           : 20px auto 0px;
	color            : #fff;
}



