
.user-collect{
	text-align : left;
}
.collect .collect-title{
	background-color : #fdb651;
	height           : 50px;
	line-height      : 50px;
	text-align       : center;
	color            : #fff;
	font-family      : myFontBoldEN;
}
.collect .item{
	padding       : 20px 0;
	margin        : 0 14px;
	border-bottom : 1px solid #ccc;
}
.collect .item-img{
	width        : 80px;
	float        : left;
	margin-right : 10px;
}
.collect .item p{
	margin : 0;
}
.collect .item .inner-title,.collect .line{
	font-family : myFontBoldEN;
}
.collect .content .inner-label{
	width    : 100px;
	overflow : hidden;
}
.collect .content .rate{
	margin-left : 5px;
}
.collect .content .right{
	float            : right;
	position         : relative;
	bottom           : 20px;
	color            : #fff;
	background-color : rgb(29,137,250);
}
.collect .right button{
	background-color : rgb(29,137,250);
	border           : none;
}
.collect .right .total{
	width            : 20px;
	line-height      : 32px;
	background-color : #fff;
	display          : inline-block;
	text-align       : center;
	outline          : none;
	color            : #333;
}
.collect .line{
	display     : inline-block;
	line-height : 24px;
}



